* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.country{
  display: flex;
  flex-direction: row;
  align-items: baseline;
}

.country-flag{
  margin-right: 16px;
  width: 40px;
  height: auto;
}

.country-many--name{
  font-size: 16px;
  font-weight: normal;
}

.country-container{
  list-style: none;
  padding: 0;
}

.country-description{
  font-size: 20px;
  font-weight: bold;
}
.country-info{
  font-weight: normal;

}
.country-list{
  list-style: none;
  display: flex;
  align-items: baseline;
  flex-direction: column;
  padding: 0;
}